@font-face {
  font-family: "Material Icons Round";
  font-style: normal;
  font-weight: 400;
  font-display: block;
  src: url("../../fonts/material-icon/MaterialIcons-Round.ttf");
  /* For IE6-8 */
  src: url("../../fonts/material-icon/MaterialIcons-Round.woff2") format("woff2"), url("../../fonts/material-icon/MaterialIcons-Round.woff") format("woff"), url("../../fonts/material-icon/MaterialIcons-Round.ttf") format("truetype");
}

.material-icons {
  font-family: "Material Icons Round";
  font-weight: normal;
  font-style: normal;
  font-size: 22px;
  vertical-align: middle;
  display: inline-block;
  line-height: 0.9;
  text-transform: none;
  letter-spacing: normal;
  -ms-word-wrap: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
}

.material-icons:before {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  opacity: .3;
}

.material-icons:after {
  opacity: 1;
  line-height: normal;
}

/* Rules for sizing the icon. */
.md-18 {
  font-size: 18px;
}

.md-24 {
  font-size: 24px;
}

.md-28 {
  font-size: 28px;
}

.md-36 {
  font-size: 36px;
}

.md-48 {
  font-size: 48px;
}

/* Rules for using icons as black on a light background. */
.md-dark {
  color: rgba(0, 0, 0, 0.54);
}

.md-dark.md-inactive {
  color: rgba(0, 0, 0, 0.26);
}

/* Rules for using icons as white on a dark background. */
.md-light {
  color: white;
}

.md-light.md-inactive {
  color: rgba(255, 255, 255, 0.3);
}

